import { useEffect, useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from 'react-router-dom';
import Price from './Price.js'

function Productinfo({product}){
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    useEffect(() => {
        handleSelect();
    }, []);
    return (
        <div
            className="product-modal"
            role="dialog"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-5">
                                <h3>No.{product.num} <br/>{product.name}</h3>
                                <div className={product.rental ? "대여가능" : "대여중"}>{product.rental ? "대여가능" : "대여중"}</div>
                                    <Carousel activeIndex={index} onSelect={handleSelect} indicators={true}>
                                        { product.imgSrc ? product.imgSrc.map((img) => (
                                            <Carousel.Item key={img}>
                                                <img
                                                    className="d-block w-100"
                                                    src={img}
                                                    alt={img}
                                                />
                                            </Carousel.Item>
                                        ))
                                            : null
                                        }
                                    </Carousel>
                                    <table border="solid">
                                        <tbody>
                                            <tr>
                                                <th>캐리어번호</th>
                                                <td>{product.num}</td>
                                            </tr>
                                            <tr>
                                                <th>캐리어이름</th>
                                                <td>{product.name}</td>
                                            </tr>
                                            <tr>
                                                <th>사이즈</th>
                                                <td>{product.type}/{product.size}인치</td>
                                            </tr>
                                            <tr>
                                                <th>오픈타입</th>
                                                <td>{product.zip ? "지퍼" : "원터치"}</td>
                                            </tr>
                                            <tr>
                                                <th>확장여부</th>
                                                <td>{product.extension ? "가능" : "불가"}</td>
                                            </tr>
                                            <tr>
                                                <th>바퀴갯수</th>
                                                <td>{product.wheels}개</td>
                                            </tr>
                                            <tr>
                                                <th>사이드핸들</th>
                                                <td>{product.sidehandle ? "있음" : "없음"}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    {product.type === "미니캐리어" ?
                                        <p>*미니캐리어는 개별대여가 불가하며 일반캐리어 대여 시 추가가능합니다.(+2,000원)</p>
                                        :
                                        (product.rental ?
                                            <Link
                                                to={process.env.PUBLIC_URL + `/product-order/${product.id}`}
                                            >
                                                <button
                                                    className="btn btn-primary btn-xl text-uppercase"
                                                    type="button"
                                                >
                                                    주문하기
                                                </button>
                                            </Link>
                                            : <p>*현재 대여 중인 캐리어입니다.</p>)
                                    }
                                <Price />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Productinfo;